.CardAir {
    margin: 10px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
    transition: transform 100ms ease-in;
    cursor: pointer;
  }

.CardAir:hover {
  transform: scale(1.03);
}
  
.CardAir > img {
    object-fit: fill;
    min-width: 300px;
    min-height: 200px;
    width: 100%;
  }

.CardAir__info {
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    padding-top: 20px;
    border: 1;
  }
 
.CardAir__info > h2 {
    font-size: 18px;
    font-weight: 600;
  }
  
.CardAir__info > h4 {
    font-size: 14px;
    font-weight: 300;
    margin-top: 8px;
    margin-bottom: 8px;
  }
  
.CardAir__info > h3 {
  font-size: 16px;
}

/* Extra */
.CardAir__info > h3:hover{
  color: goldenrod;
}

.star {
  color: goldenrod;
}
.star:hover{
  color: gray;
}