.search {
    position: absolute;
    top: 35px;
    left: 5%;
    /* width: 100vw; */
    width: 50vw;
    background-color: yellow;
}

.search > h2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* width: 559px; */
    width: 559px;
    /* padding: 10px; */
    padding:40px;
    background-color: white;
    position: absolute;
    left: 0;
    top: 380px;
    height: 30px;
}

.search > input {
    width: 539px;
    padding: 20px;
    /* position: absolute; */
    left: 0;
    height: 30px;
    top: 420px;
    border: none;
  }
search > 
.input:focus {
    outline-width: 0;
}

.search > button {
    position: absolute;
    left: 0;
    top: 480px;
    text-transform: inherit !important;
    background-color: #ff7779;
    color: white;
    width: 579px;
  }

.search > button:hover {
    background-color: white;
    color: #ff7779;
}