.banner {
  background: url("https://www.tazzerclean.co.uk/assets/img/aboutus1.8d9172eb.png");
  /* background: violet; */
  /* height: 100vh;  */
  height: 50vh;  
  position: relative;
  display: flex;
  /* background-repeat: no-repeat; */
  
}

.banner__info {
    background-color: black;
    color: white;
    padding-top: 25vh;
    /* padding-left: 50px; */
    /* padding-right: 50px; */
    padding-bottom: 40px;
    width: 300px;
}

.banner__search {
  display: flex;
  flex-direction: column;
}

.banner__searchButton {
  background-color: yellow !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.banner__info > button {
    background-color: #ff7779;
    color: white;
    text-transform: inherit;
    margin-top: 20px;
    font-weight: 600;
}

.banner__info > button:hover {
  background-color: white;
  color: #ff7779;
}

.banner__info > h5 {
  margin-bottom: 10px;
}